@font-face {
    font-family: 'SofiaProRegular';
    src: url('../fonts/sofia-pro/sofia-pro-regular/webfonts/sofia-pro-regular.eot');
    src: url('../fonts/sofia-pro/sofia-pro-regular/webfonts/sofia-pro-regular.eot?#iefix') format('embedded-opentype'),
         url('../fonts/sofia-pro/sofia-pro-regular/webfonts/sofia-pro-regular.woff2') format('woff2'),
         url('../fonts/sofia-pro/sofia-pro-regular/webfonts/sofia-pro-regular.woff') format('woff'),
         url('../fonts/sofia-pro/sofia-pro-regular/webfonts/sofia-pro-regular.ttf') format('truetype'),
         url('../fonts/sofia-pro/sofia-pro-regular/webfonts/sofia-pro-regular.svg#youworkforthem') format('svg');
    font-weight: normal;
    font-style: normal;
}



@font-face {
    font-family: 'SofiaProExtraLight';
    src: url('../fonts/sofia-pro/sofia-pro-extralight/webfonts/sofia-pro-extralight.eot');
    src: url('../fonts/sofia-pro/sofia-pro-extralight/webfonts/sofia-pro-extralight.eot?#iefix') format('embedded-opentype'),
         url('../fonts/sofia-pro/sofia-pro-extralight/webfonts/sofia-pro-extralight.woff2') format('woff2'),
         url('../fonts/sofia-pro/sofia-pro-extralight/webfonts/sofia-pro-extralight.woff') format('woff'),
         url('../fonts/sofia-pro/sofia-pro-extralight/webfonts/sofia-pro-extralight.ttf') format('truetype'),
         url('../fonts/sofia-pro/sofia-pro-extralight/webfonts/sofia-pro-extralight.svg#youworkforthem') format('svg');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'KroppenRound-Regular';
    src: url('../fonts/kroppen-round-full/kroppen-round-regular/webfonts/kroppen-round-regular.eot');
    src: url('../fonts/kroppen-round-full/kroppen-round-regular/webfonts/kroppen-round-regular.eot?#iefix') format('embedded-opentype'),
         url('../fonts/kroppen-round-full/kroppen-round-regular/webfonts/kroppen-round-regular.woff2') format('woff2'),
