// =====================================================================
// Footer
// =====================================================================

.section-partners {
    position: relative;
    z-index: 4;
    text-align: center;
    background-color: $color-off-white;

    .footer {
        padding-top: 120px;
        padding-bottom: 60px;
        background-color: $color-off-white;
    }
}


    .partners-text {
        max-width: 620px;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 90px;

        padding-left: 15px;
        padding-right: 15px;
        @include media-breakpoint-up(sm) {
            padding-left: 0;
            padding-right: 0;
        }
    }
        .partners-title {
            @include font-size(3.5,40);
            letter-spacing: 0.025em;
            text-transform: uppercase;
            color: $color-dark-grey;
            margin-bottom: 30px;
        }
        .partners-description {
            @include font-size(1.8,24);
            letter-spacing: 0.025em;
            color: $color-medium-grey;
        }

    .partners-logo {
        margin-bottom: 15px;
        height: 90px;
        width: 200px;
        @include media-breakpoint-down(sm) {
            width: auto;
        }

        .no-touchevents & {
            &:hover {
                .partners-img.bw {
                    // opacity: 0;
                }
                .partners-img.color {
                    opacity: 1;
                }
            }
        }
    }
        .partners-logo-link {
            position: relative;
            display: block;
        }
            .partners-img {
                position: absolute;
                top: 0;
                left: 0;
                bottom: 0;
                right: 0;
                margin: auto;
                transition: opacity 0.3s ease-out;
                &.bw {
                    opacity: 1;
                }
                &.color {
                    opacity: 0;
                }
            }

    .community {
        margin-top: 150px;


        br {
            display: block;
        }
        margin-bottom: 60px;
        @include media-breakpoint-up(sm) {
            margin-bottom: 120px;
            br {
                display: none;
            }
        }
    }
        .community-link {
            display: inline-block;
            background-color: $color-orange;
            font-family: $font-sofiaproregular;
            color: $color-white;
            padding: 0 40px;
            border-radius: 4px;
            @include font-size(1.6,45);
            letter-spacing: 0.025em;

            transition: background-color 0.3s ease-out;

            .no-touchevents & {
                &:hover {
                    background-color: darken( $color-orange, $state-hover );
                }
            }
        }

.footer-address {

    @include font-size(0,'normal');
    padding-top: 45px;
    text-align: center;

    @include media-breakpoint-down(sm) {
        border-top: solid 1px rgba(0,0,0,0.1);
        padding-left: $splash-spacing-mobile - 15px;
        padding-right: $splash-spacing-mobile - 15px;
    }
    @include media-breakpoint-down(lg) {
    }
}
    .footer-logo-img {
        display: inline-block;
        vertical-align: top;
        margin-top: 4px;
        margin-right: 20px;
    }
    .footer-text {
        display: inline-block;
        vertical-align: top;

        @include font-size(1.6,20);
        letter-spacing: 0.025em;
        color: $color-medium-grey;

        p {
            margin: 0;
        }
    }