@-webkit-keyframes float {
    from { transform: translate(0,8px) rotate(-135deg); }
    to { transform: translate(0,0) rotate(-135deg); }
}
@-moz-keyframes float {
    from { transform: translate(0,8px) rotate(-135deg); }
    to { transform: translate(0,0) rotate(-135deg); }
}
@-ms-keyframes float {
    from { transform: translate(0,8px) rotate(-135deg); }
    to { transform: translate(0,0) rotate(-135deg); }
}
@-o-keyframes float {
    from { transform: translate(0,8px) rotate(-135deg); }
    to { transform: translate(0,0) rotate(-135deg); }
}
@keyframes float {
    from { transform: translate(0,8px) rotate(-135deg); }
    to { transform: translate(0,0) rotate(-135deg); }
}




@-webkit-keyframes fillup {
    from { width:6px; }
    to { width:100%; }
}
@-moz-keyframes fillup {
    from { width:6px; }
    to { width:100%; }
}
@-ms-keyframes fillup {
    from { width:6px; }
    to { width:100%; }
}
@-o-keyframes fillup {
    from { width:6px; }
    to { width:100%; }
}
@keyframes fillup {
    from { width:6px; }
    to { width:100%; }
}


