#intro-arrow {
    position: relative;
    text-align: center;
    z-index: 9;
    width: 100%;
    position: fixed;
    bottom: 0;

    visibility: hidden; // mod by js
    opacity: 0; // mod by js

    .title {
        position: absolute;
        bottom: 54px;
        left: 0;
        right: 0;
        margin-left: auto;
        margin-right: auto;
        font-family: $font-sofiaproregular;
        color: $color-light-grey;
        text-transform: uppercase;
        @include font-size(1.8,16);
        letter-spacing: 0.05em;
    }

    .arrow-bounds {
        bottom: 0;
        .arrow {
            border-left: $color-orange solid 1px;
            border-top: $color-orange solid 1px;
            transition: none;
        }
    }
}



.arrow-bounds {

    position: absolute;
    left: 50%;
    z-index: 9;
    cursor: pointer;
    padding: 30px;

    // outline: solid 1px rgba(255,0,0,0.2);

    bottom: 5px;
    margin-left: -45px;
    @include media-breakpoint-up(sm) {
        bottom: 30px;
        margin-left: -45px;
    }


    .no-touchevents & {

        &:hover {
            .arrow {
                // animation: float-back 3s ease-out forwards;

                animation-play-state: paused;

                &:before,
                &:after {
                    transition: width 0.4s ease-out, height 0.4s ease-out;
                }
                &:before {
                    height: 100%;
                }
                &:after {
                    width: 100%;
                }
            }
        }
    }
}

    .arrow {

        border-left: $color-medium-grey solid 1px;
        border-top: $color-medium-grey solid 1px;
        animation: float 3s ease-in-out infinite alternate;
        transition: transform 0.3s ease-out;

        // background-color: red;


        &:before,
        &:after {
            position: absolute;
            top: 0;
            left: 0;
            content: '';
            background-color: $color-medium-grey;
            transform-origin: top left;
            transition: width 0.15s ease-in, height 0.15s ease-in;
        }
        &:before {
            width: 1px;
            height: 0;
        }
        &:after {
            width: 0;
            height: 1px;
        }


        .arrow-link {

            // width: 100%;
            // height: 100%;
            display: block;

            width: 20px;
            height: 20px;
            @include media-breakpoint-up(sm) {
                width: 30px;
                height: 30px;
            }
        }

    }




