    .db {
        width: 100%;
        height: 100%;
    }
        .db-content {
            position: relative;
            width: 100%;
            height: 100%;
            overflow: hidden;
        }
            // parallax on holder instead to avoid bottleneck on bgsize cover
            .db-bg-holder,
            .db-fg-holder {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
            }

            .db-bg-holder {

            }
            .db-fg-holder {
                display: none; // drop parallax
            }

