.exposure-nav {
    position: absolute;
    z-index: 9;

    bottom: $splash-spacing-mobile;
    left: $splash-spacing-mobile;
    // border: solid 1px green;

    @include media-breakpoint-up(sm)  {
        bottom: $splash-spacing-tablet;
        left: $splash-spacing-tablet;
        // border: solid 1px red;

        // @include ipad() {
        //     bottom: $splash-spacing-tablet;
        //     left: $splash-spacing-tablet;
        //     border: solid 1px red;
        // }
    }
    @include media-breakpoint-up(lg) {
        bottom: $splash-spacing-desktop;
        left: $splash-spacing-desktop;
        // border: solid 1px blue;
    }


    @include media-breakpoint-down(sm) {
        @include landscape() {
            bottom: 30px;
        }
    }

}
    .exposure-nav-list {

    }
        .exposure-nav-item {
            position: relative;
            display: inline-block;
            cursor: pointer;
            width: 6px;
            height: 6px;
            border-radius: 3px;
            background-color: $color-light-grey;
            margin-right: 12px;

            transition: background-color 0.3s ease-out, transform 0.3s ease-out, width 0.7s cubic-bezier(.5,.01,.1,1);



            &.is-active {
                width: 55px;

                .stop-cycle & {
                    background-color: $color-medium-grey;
                }

                .exposure-nav-item-loader {
                    // transition: width 4.5s ease-in-out;
                    // width: 100%;
                    background-color: $color-medium-grey;
                    animation: fillup 4.5s ease-in-out forwards;

                    // .stop-cycle & {
                    //     animation: none;
                    //     transition: width 0.3s ease-out;
                    //     width: 100%;
                    // }
                }
            }
            &.is-paused {
                .exposure-nav-item-loader {
                    animation-play-state: paused;
                }
            }
            // &.is-active.is-reset {
            //     width: 55px;
            //     .exposure-nav-item-loader {
            //         width: 6px;
            //         transition: width 0.4s ease-out;
            //     }
            // }
            .no-touchevents & {
                &:hover {
                    transform: scale(1.5);
                    background-color: $color-medium-grey;

                    &.is-active {
                        transform: scale(1);
                        background-color: $color-light-grey;
                    }
                }
            }

        }
            .exposure-title {
