// logo-sprite
// 390px
@-webkit-keyframes logo-sprite {
    from { background-position: 0 0; }
    to { background-position: 0 -390px; }
}
@-moz-keyframes logo-sprite {
    from { background-position: 0 0; }
    to { background-position: 0 -390px; }
}
@-ms-keyframes logo-sprite {
    from { background-position: 0 0; }
    to { background-position: 0 -390px; }
}
@-o-keyframes logo-sprite {
    from { background-position: 0 0; }
    to { background-position: 0 -390px; }
}
@keyframes logo-sprite {
    from { background-position: 0 0; }
    to { background-position: 0 -390px; }
}



