    // http://stackoverflow.com/questions/19504330/css3-keyframe-animations-end-and-stay-on-the-last-frame
    .logo {
        width: 105px;
        height: 65px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-size: 100% 600%;
        background-image: url('../images/common/logo-sprite@2x.png');
        // animation: logo-sprite 1.2s 1.5s steps(6) 1;
        // animation: logo-sprite 1.2s 1.5s steps(6) infinite forwards;
        animation: logo-sprite 1.2s 1.5s steps(6) infinite alternate;


        position: absolute;
        z-index: 9;
        top: $splash-spacing-mobile;
        left: $splash-spacing-mobile;
        @include media-breakpoint-up(sm) {
            top: $splash-spacing-desktop;
            left: $splash-spacing-desktop;
            @include ipad() {
                top: $splash-spacing-tablet;
                left: $splash-spacing-tablet;
            }
        }


        @include media-breakpoint-down(sm) {
            @include landscape() {
                top: 30px;
                left: $splash-spacing-tablet;
            }
        }
    }

